body { margin: 0; overflow: hidden; touch-action: none; }
canvas { display: block; }
.menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .menu {
    background: #739c2f;
    padding: 20px;
    border-radius: 3px;
    text-align: center;
  }
  
  .ui-panel {
    position: absolute;
    top: 10px;
    left: 10px;
  }
  